import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { setNewsDetail as setNewsDetailAction } from "../../redux/actions/news";
import news1 from "../../images/news/news-1.jpg";
import news2 from "../../images/news/news-2.jpg";
import news3 from "../../images/news/news-3.jpg";

const NewsContainer = ({
  isLast,
  currentNews,
  keyIdx,
  lang,
  setNewsDetail
}) => {
  const newsImg = [news1, news2, news3];
  const setDetail = data => {
    setNewsDetail(data);
  };
  return (
    <div
      key={keyIdx}
      className={
        isLast ? "news-box animate-image lastcols" : "news-box animate-image"
      }
    >
      <div className="news-thumb">
        <figure className="news-thumb-wrap">
          <img src={newsImg[keyIdx]} alt=" " />
        </figure>
      </div>
      <div className="news-desc">
        <div className="inner-news-desc">
          <div className="post-meta">
            <span>
              By{" "}
              <a href="#muine" title="Posts by travern" rel="author">
                travern
              </a>
            </span>{" "}
            <span> - 17 Sep</span>{" "}
            <span>
              {" "}
              /{" "}
              <a href="#muine" title="View all posts in Guide">
                Guide
              </a>
            </span>{" "}
            <span>
              {" "}
              / Comments (<a href="#muine">0</a>)
            </span>
          </div>
          <h4>
            <a href="#muine">{currentNews.name && currentNews.name[lang]}</a>
          </h4>
          <p>
            {currentNews.short_description &&
              currentNews.short_description[lang]}
          </p>

          <Link
            className="room-title yellow-text"
            to={`/newsDetail/${currentNews.id}`}
            onClick={() => setDetail(currentNews.id)}
          >
            Read More
          </Link>
          {/* <a href="#muine" className="main-button" style={{ color: "#CE9B51" }}>
            Read More
          </a> */}
        </div>
      </div>
    </div>
  );
};

export default connect(
  state => {
    return {
      new: state.muineService.serviceDetail,
      news: state.muineService.list,
      lang: state.lang.currentLang
    };
  },
  dispatch => ({
    setNewsDetail: data => dispatch(setNewsDetailAction(data))
  })
)(NewsContainer);
